var wx;
//
function wxRead(callback){
    document.addEventListener("WeixinJSBridgeReady",function() {
        callback();
    },false);
}

function wxConfig(vAppId,vTimestamp,vNoncestr,vSignature){
    wx.config({
        debug: false, // ,apialertpclogpc
        appId: vAppId, // 
        timestamp: vTimestamp, // 
        nonceStr: vNoncestr, // 
        signature: vSignature,// 1
        jsApiList: ["onMenuShareAppMessage","onMenuShareTimeline","hideMenuItems","showMenuItems"] // JSJS2
    });
}

function wxReady(callback){
    wx.ready(function(){
        callback();
    });
}

//
function shareTimeline(vTitle,vLink,vImgUrl,vSuccessCallback,vCancelCallback){
    wx.onMenuShareTimeline({
        title: vTitle, // 
        link: vLink, // JS
        imgUrl: vImgUrl, // 
        success: function () {
            if(vSuccessCallback) {
                vSuccessCallback();
            }
        },
        cancel: function () {
            // 
            if(vCancelCallback) {
                vCancelCallback();
            }
        }
    });
}

//
function shareAppMessage(vTitle,vDesc,vLink,vImgUrl,vType,vDataUrl,vSuccessCallback,vCancelCallback){
    wx.onMenuShareAppMessage({
        title: vTitle, // 
        desc: vDesc, // 
        link: vLink, // JS
        imgUrl: vImgUrl, // 
        type: vType, // ,musicvideolinklink
        dataUrl: vDataUrl, // typemusicvideo
        success: function () {
            // 
            vSuccessCallback();
        },
        cancel: function () {
            // 
            vCancelCallback();
        }
    });
}

//
function weixinPay(pAppid,pTimeStamp,pNonceStr,pPackage,pSign,url){
    WeixinJSBridge.invoke(
        'getBrandWCPayRequest', {
            "appId":pAppid,     //
            "timeStamp":pTimeStamp,         //1970
            "nonceStr":pNonceStr, //
            "package":pPackage,
            "signType":"MD5",         //
            "paySign":pSign //
        },
        function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok" ) {
                //alert
                window.location.href=url;
            }     // ,res.err_msg    ok
        }
    );
}

//
function hideMenuItems(pArr){
    wx.hideMenuItems({
        menuList: pArr,
        success:function(ret){

        },
        fail:function(ret){
        }
    });
}

//
function showMenuItems(pArr){
    wx.showMenuItems({
        menuList: pArr
    });
}

export {
    weixinPay
}