function request(url,method="GET",data={}){
    return new Promise((resolve, reject)=>{
        // setTimeout(()=>{
        //     uni.showLoading({
        //         title: '...'
        //     });
        // },30);
        uni.request({
            url: url,
            method:method.toLocaleUpperCase(),
            data: data,
            header:{"Content-Type": "application/x-www-form-urlencoded"},
            success: (res) => {
                resolve(res.data);
                // setTimeout(()=>{
                //     uni.hideLoading();
                // },30);
            },
            fail:(res)=>{
                reject(res);
                // setTimeout(()=>{
                //     uni.hideLoading();
                // },30);
            }
        });
    })

}

export {
    request
}