function bezier(points, times){
    // 03A,B,C,ABD,BCE,DEF,F
    // 1
    // 2,DABEBC
    // 3100msD,E, FDEAD/AB = DF/DE
    // 4DEDEF
    // AB
    var bezier_points = [];
    var points_D = [];
    var points_E = [];
    const DIST_AB = Math.sqrt(Math.pow(points[1]['x'] - points[0]['x'], 2) + Math.pow(points[1]['y'] - points[0]['y'], 2));
    // BC
    const DIST_BC = Math.sqrt(Math.pow(points[2]['x'] - points[1]['x'], 2) + Math.pow(points[2]['y'] - points[1]['y'], 2));
    // DAB
    const EACH_MOVE_AD = -(DIST_AB / times);
    // EBC
    const EACH_MOVE_BE = -(DIST_BC / times);
    // AB
    const TAN_AB = (points[1]['y'] - points[0]['y']) / (points[1]['x'] - points[0]['x']);
    // BC
    const TAN_BC = (points[2]['y'] - points[1]['y']) / (points[2]['x'] - points[1]['x']);
    // AB
    const RADIUS_AB = Math.atan(TAN_AB);
    // BC
    const RADIUS_BC = Math.atan(TAN_BC);
    // 
    for (var i = 1; i <= times; i++) {
        // AD
        var dist_AD = EACH_MOVE_AD * i;
        // BE
        var dist_BE = EACH_MOVE_BE * i;
        // D
        var point_D = {};
        point_D['x'] = dist_AD * Math.cos(RADIUS_AB) + points[0]['x'];
        point_D['y'] = dist_AD * Math.sin(RADIUS_AB) + points[0]['y'];
        points_D.push(point_D);
        // E
        var point_E = {};
        point_E['x'] = dist_BE * Math.cos(RADIUS_BC) + points[1]['x'];
        point_E['y'] = dist_BE * Math.sin(RADIUS_BC) + points[1]['y'];
        points_E.push(point_E);
        // DE
        var tan_DE = (point_E['y'] - point_D['y']) / (point_E['x'] - point_D['x']);
        // tan_DE
        var radius_DE = Math.atan(tan_DE);
        // DE
        var dist_DE = Math.sqrt(Math.pow((point_E['x'] - point_D['x']), 2) + Math.pow((point_E['y'] - point_D['y']), 2));
        // DF
        var dist_DF = (dist_AD / DIST_AB) * dist_DE;
        // DF
        var point_F = {};
        point_F['x'] = dist_DF * Math.cos(radius_DE) + point_D['x'];
        point_F['y'] = dist_DF * Math.sin(radius_DE) + point_D['y'];
        bezier_points.push(point_F);
    }
    return {
        'bezier_points': bezier_points
    };
}
function safeUser(pThis,branch_shop_id="",table_code=""){
    pThis.$store.dispatch("user/safeUser",{branch_shop_id:branch_shop_id,success:(res)=>{
            if(res.code!==200){
                pThis.$store.commit("user/OUT_LOGIN");
                uni.navigateTo({
                    url:"/pages/login/index?branch_shop_id=" + branch_shop_id + "&table_code=" + table_code
                })
            }
        }});
}

function localParam(search, hash) {
    var fn = function(str, reg) {
        if (str) {
            var data = {};
            str.replace(reg, function($0, $1, $2, $3) {
                data[$1] = $3;
            });
            return data;
        }
    }
    return {
        search : fn(search, new RegExp("([^?=&]+)(=([^&]*))?", "g")) || {},
        hash : fn(hash, new RegExp("([^#=&]+)(=([^&]*))?", "g")) || {}
    };
}
export default {
    bezier,
    safeUser,
    localParam
}